export function variableParser(
  content: string,
  variableData: Record<string, any> = {},
  replaceWith: string | null = null
): string {
  let result: string = content;
  const reg = RegExp("{[^{}]*}", "g");
  let arr: RegExpExecArray | null;

  while ((arr = reg.exec(content))) {
    const variableName = arr[0].slice(1, -1).trim();
    let variableValue = variableData[variableName];

    if (variableValue === undefined) {
      if (replaceWith === null) continue;
      variableValue = `"${replaceWith}"`;
    }

    const variableNameRegex = new RegExp(
      `{\\s*${variableName.replace(/[.*+?^${}()|[\]\\]/g, "\\$&")}\\s*}`
    );
    result = result.replace(variableNameRegex, String(variableValue));
  }
  return typeof result === "string" ? result.trim() : result;
}
